import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../styles/header.css';
import Cookies from 'universal-cookie';

const cookies = new Cookies();

class Header extends Component {
  onLogOut(e) {
    cookies.remove('username');
    cookies.remove('token');
  }

  render() {
    return (
      <div className="header cf">
        <div className="wrapper">
          <div className="logo">
            <img className="image" src={this.props.logo} alt="logo" />
            <h2 className="title">{this.props.title}</h2>
          </div>
          <div className="menu">
            <Link className="option" to="/">Home</Link>
            <Link className="option" to="/">Sign up</Link>
            <Link className="option" to="/login">Log in</Link>
            <Link className="option" onClick={this.onLogOut.bind(this)}>Log out</Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;


